/* General Layout */
.home-layout {
    display: flex;
    height: 100vh;
    font-family: Arial, sans-serif;
}

.sidebar {
    width: 250px;
    background-color: #2d2d2d;
    display: flex;
    flex-direction: column;
    padding: 20px;
    color: white;
}

.sidebar .logo {
    font-size: 1.8em;
    margin-bottom: 20px;
    text-align: center;
    font-weight: bold;
}

.sidebar-menu {
    list-style: none;
    padding: 0;
    margin: 0;
}

.sidebar-menu li {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    color: white;
    transition: background-color 0.3s;
}

.sidebar-menu li:hover {
    background-color: #404040;
}

.menu-icon {
    margin-right: 10px;
    font-size: 20px;
}

