/* Login page styling */
.login-page {
    display: flex;
    height: 100vh;
    font-family: Arial, sans-serif;
}

/* Left section */
.login-left {
    flex: 1;
    background-color: #805050;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 20px;
}

.login-graphic img {
    max-width: 80%;
    height: auto;
}

.login-graphic h1 {
    font-size: 1.8rem;
    margin: 20px 0;
}

.login-graphic p {
    font-size: 1.2rem;
}

/* Right section */
.login-right {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-box {
    width: 80%;
    max-width: 400px;
    text-align: left;
}

h2 {
    font-size: 1.8rem;
    margin-bottom: 20px;
}

label {
    display: block;
    font-size: 1rem;
    margin-bottom: 5px;
}

input {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
}

button {
    width: 100%;
    padding: 10px;
    background-color: #805050;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
}

button:hover {
    background-color: #6e4242;
}

.forgot-password {
    margin-top: 10px;
    font-size: 0.9rem;
    text-align: center;
    color: #805050;
    cursor: pointer;
}

.error-message {
    color: red;
    font-size: 0.9rem;
}
